// DO NOT USE this file will be removed in a future release
import { css } from 'lit';
import { state } from 'lit/decorators.js';
import { OmniElement } from '../../omni-element.js';

/** @element omni-origin */
export class OmniOrigin extends OmniElement {
  static get styles() {
    return [
      css`
        :host {
          display: contents;
        }
      `
    ];
  }

  @state() _target: any = null;

  constructor() {
    super();
    this.attachShadow({ mode: 'open' }).innerHTML = `<slot></slot>`;
  }

  get target() {
    return this._target ?? null;
  }

  set target(node) {
    const { target } = this;
    if (!node) {
      if (target && target.origin) {
        target.origin = null;
        target.removeAttribute('origin');
      }
      this.removeAttribute('target');
      this._target = null;
      this.remove();

      return;
    }

    if (node.nodeType !== 1) {
      throw new Error(`invalid node type ${node.nodeType}`);
    }

    if (this.target) {
      throw new Error(`origin has a target ${target.localName}`);
    }

    this._target = node;
    this.setAttribute('target', node.localName);
    node.origin = this;
    node.setAttribute('origin', this.localName);

    node.before(this);
  }

  connectedCallback() {
    super.connectedCallback();
    this.dispatchEvent(new CustomEvent('origin', { detail: this, composed: true, cancelable: true, bubbles: true }));
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    this.target = null;
  }

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  render() {}
}

OmniElement.register('omni-origin', OmniOrigin);

declare global {
  interface HTMLElementTagNameMap {
    'omni-origin': OmniOrigin;
  }
}
